<template>
  <div class="dete-input-config">
    <Form :label-width="120">
      <div class="config"
           v-for="(item, index) in currentValue" :key="index">
        <!-- 别名 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'alias' && cType !== 1">
          <Input v-model="item.configData.configData"></Input>
        </Form-item>
        <!-- 日期格式 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'format'">
          <Radio-group v-model="formatSelectVal"
                       @on-change="formatSelectChange"
                       vertical>
            <Radio label="yyyy">
              <span>年 (例如：2019年)</span>
            </Radio>
            <Radio label="yyyy-MM">
              <span>年月 (例如：2019年12月)</span>
            </Radio>
            <Radio label="yyyy-MM-dd">
              <span>年月日 (例如：2019年12月30日)</span>
            </Radio>
            <Radio label="yyyy-MM-dd HH:mm:ss">
              <span>年月日时分秒 (例如：2018年07月03日 10:05:50)</span>
            </Radio>
            <Radio label="other">
              <span>其它</span>
              <Input v-model="otherData"
                     @on-focus="otherDataFocus"
                     @on-change="otherDataChange"
                     placeholder="请输入..."
                     style="width: 230px"></Input>
            </Radio>
          </Radio-group>
          <!-- {{ item.configData }} -->
          <p style="margin-top: 10px">
            <Button @click="currentVisible = true">常见的日期和时间格式</Button>
          </p>
        </Form-item>
        <!-- 日期范围 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'range'">
          <DatePicker :value="rangeDefaultVal"
                          :type="rangeType"
                          v-if="rangeType !== 'year' && rangeType !== 'month' && rangeType !== 'other'"
                          :format="currentValue[formatN].configData.configData"
                          style="width: 100%"
                          confirm
                          @on-change="changeRange">
          </DatePicker>
          <p v-else>当日期格式为“年”、”年月“或其它自定义格式时，日期范围只能使用代码编程实现。</p>
          <p><strong>提示：</strong></p>
          <p>“今天之前”，则代码输出为：&lt;today</p>
          <p>“今天及之前”，则代码输出为：&lt;=today</p>
          <p>“今天之后”，则代码输出为：&gt;today</p>
          <p>“今天及之后”，则代码输出为：&gt;=today</p>
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
        <!-- 默认值 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'value'">
          <DatePicker :value="valueDefaultVal"
                          :type="valueType"
                          :options="options"
                          v-if="valueType !== 'other'"
                          :format="currentValue[formatN].configData.configData"
                          style="width: 100%"
                          @on-change="changeDefault">
          </DatePicker>
          <p v-else>当日期格式为自定义格式时，默认值只能使用代码编程实现。</p>
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
      </div>
    </Form>
    <Modal v-model="currentVisible"
           title="常见的日期和时间格式">
      <div class="api">
        <table>
          <thead>
            <tr>
              <th>名称</th>
              <th>说明</th>
              <th>示例</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>yyyy</td>
              <td>年份（四位）</td>
              <td>2016</td>
            </tr>
            <tr>
              <td>yy</td>
              <td>年份（两位）</td>
              <td>16</td>
            </tr>
            <tr>
              <td>MM</td>
              <td>月份（两位）</td>
              <td>01</td>
            </tr>
            <tr>
              <td>M</td>
              <td>月份（一位）</td>
              <td>1</td>
            </tr>
            <tr>
              <td>MMMM</td>
              <td>月份（英文）</td>
              <td>January</td>
            </tr>
            <tr>
              <td>MMM</td>
              <td>月份（英文简写）</td>
              <td>Jan</td>
            </tr>
            <tr>
              <td>dd</td>
              <td>日期（两位）</td>
              <td>01</td>
            </tr>
            <tr>
              <td>d</td>
              <td>日期（一位）</td>
              <td>1</td>
            </tr>
            <tr>
              <td>Do</td>
              <td>日期（简写）</td>
              <td>1st</td>
            </tr>
            <tr>
              <td>DD</td>
              <td>星期（两位）</td>
              <td>00</td>
            </tr>
            <tr>
              <td>D</td>
              <td>星期（一位）</td>
              <td>0</td>
            </tr>
            <tr>
              <td>dddd</td>
              <td>星期（英文）</td>
              <td>Monday</td>
            </tr>
            <tr>
              <td>ddd</td>
              <td>星期（英文简写）</td>
              <td>Mon</td>
            </tr>
            <tr>
              <td>HH</td>
              <td>小时（24小时制两位）</td>
              <td>01</td>
            </tr>
            <tr>
              <td>H</td>
              <td>小时（24小时制一位）</td>
              <td>1</td>
            </tr>
            <tr>
              <td>hh</td>
              <td>小时（12小时制两位）</td>
              <td>01</td>
            </tr>
            <tr>
              <td>h</td>
              <td>小时（12小时制一位）</td>
              <td>1</td>
            </tr>
            <tr>
              <td>mm</td>
              <td>分钟（两位）</td>
              <td>01</td>
            </tr>
            <tr>
              <td>m</td>
              <td>分钟（一位）</td>
              <td>1</td>
            </tr>
            <tr>
              <td>ss</td>
              <td>秒钟（两位）</td>
              <td>01</td>
            </tr>
            <tr>
              <td>s</td>
              <td>秒钟（一位）</td>
              <td>1</td>
            </tr>
            <tr>
              <td>SSS</td>
              <td>毫秒（三位）</td>
              <td>019</td>
            </tr>
            <tr>
              <td>SS</td>
              <td>毫秒（两位）</td>
              <td>01</td>
            </tr>
            <tr>
              <td>S</td>
              <td>毫秒（一位）</td>
              <td>1</td>
            </tr>
            <tr>
              <td>A</td>
              <td>上午与下午（大写）</td>
              <td>AM/PM</td>
            </tr>
            <tr>
              <td>a</td>
              <td>上午与下午（小写）</td>
              <td>am/pm</td>
            </tr>
            <tr>
              <td>ZZ</td>
              <td>时区</td>
              <td>+0800</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="ivu-article">
        <p>以上是系统支持的日期格式，你可以自由组合出你需要的类型，例如：</p>
        <ul>
          <li>
            <strong>yyyy年M月d日</strong>：2016年1月1日</li>
          <li>
            <strong>MM/dd/yy</strong>：12/24/16</li>
          <li>
            <strong>H点m分s秒</strong>：9点41分0秒</li>
        </ul>
      </div>
    </Modal>
  </div>
</template>

<script>
import Editor from '@/components/editor'
import moment from 'moment'

export default {
  name: 'dete-input-config',
  components: {
    Editor
  },
  props: {
    cType: {
      type: Number,
      require: true
    },
    configs: {
      type: Array,
      require: true
    }
  },
  computed: {
    rangeType () {
      if (this.currentValue[this.formatN].configData.configData === 'yyyy-MM-dd') {
        return 'daterange'
      } else if (this.currentValue[this.formatN].configData.configData === 'yyyy-MM-dd HH:mm:ss') {
        return 'datetimerange'
      } else if (this.configs[this.formatN].configData.configData === 'yyyy') {
        return 'year'
      } else if (this.configs[this.formatN].configData.configData === 'yyyy-MM') {
        return 'month'
      } else {
        return 'other'
      }
    },
    valueType () {
      if (this.currentValue[this.formatN].configData.configData === 'yyyy-MM-dd') {
        return 'date'
      } else if (this.currentValue[this.formatN].configData.configData === 'yyyy-MM-dd HH:mm:ss') {
        return 'datetime'
      } else if (this.configs[this.formatN].configData.configData === 'yyyy') {
        return 'year'
      } else if (this.configs[this.formatN].configData.configData === 'yyyy-MM') {
        return 'month'
      } else {
        return 'other'
      }
    }
  },
  watch: {
    currentValue (newValue, oldValue) {
      this.$emit('input', {
        value: this.currentValue
      })
    }
  },
  data () {
    return {
      // uuid和currentValue是必须的，并且在第1、2位置
      uuid: '00000000-0000-0000-0000-000000000001',
      currentValue: '',
      formatN: null,
      rangeN: null,
      valueN: null,
      options: {
        disabledDate: () => {
          return false
        }
      },
      currentVisible: false,
      rangeDefaultVal: null,
      valueDefaultVal: null,
      formatDefaultVal: null,
      formatSelectVal: null,
      otherData: null
    }
  },
  created () {
    /**
     * 提示：
     * picker不能用v-model。
     * 必须打断双向绑定，否则会导致循环且数据处理的麻烦。
    */
    this.currentValue = this.deepCopy(this.configs)
    for (const n in this.currentValue) {
      if (this.currentValue[n].code === 'format') {
        if (!this.currentValue[n].configData.configData) { // 处理后端数据为null的情况
          this.currentValue[n].configData.configData = 'yyyy-MM-dd'
        }

        this.formatN = n
        this.formatDefaultVal = this.currentValue[n].configData.configData

        // 判断是否是其它选项
        if (this.formatDefaultVal !== 'yyyy' && this.formatDefaultVal !== 'yyyy-MM' && this.formatDefaultVal !== 'yyyy-MM-dd' && this.formatDefaultVal !== 'yyyy-MM-dd HH:mm:ss') {
          this.formatSelectVal = 'other'
          this.otherData = this.formatDefaultVal
        } else {
          this.formatSelectVal = this.formatDefaultVal
        }
      } else if (this.currentValue[n].code === 'range') {
        this.rangeN = n
        if (this.currentValue[n].configData.configData) {
          this.rangeDefaultVal = this.currentValue[n].configData.configData.split(' - ')
        }
        // console.log(this.currentValue[n].configData.configData)
      } else if (this.currentValue[n].code === 'value') {
        this.valueN = n
        this.valueDefaultVal = this.currentValue[n].configData.configData
      }
    }

    // 按照对应的格式格式化配置项
    this.valueDefaultVal = moment(this.valueDefaultVal).format(this.formatDefaultVal.toUpperCase())

    if (this.rangeDefaultVal && this.rangeDefaultVal.length === 2) {
      this.rangeDefaultVal[0] = moment(this.rangeDefaultVal[0]).format(this.formatDefaultVal.toUpperCase())
      this.rangeDefaultVal[1] = moment(this.rangeDefaultVal[1]).format(this.formatDefaultVal.toUpperCase())
    }

    this.options.disabledDate = (date) => {
      if (!this.currentValue[this.rangeN].configData.configData) {
        return false
      }
      const range = this.currentValue[this.rangeN].configData.configData // 这里有点诡异，数据被处理过了，说明这个函数可能是用于回调。
      if (range[0] && range[1]) {
        return !moment(moment(date.valueOf()).format('YYYY-MM-DD')).isBetween(moment(range[0]).subtract(1, 'days'), moment(range[1]).add(1, 'd'))
      } else {
        return false
      }
    }

    // setTimeout(() => {
    //   if (this.valueDefaultVal) this.currentValue[this.valueN].configData.configData = this.valueDefaultVal
    //   if (this.rangeDefaultVal && typeof(this.rangeDefaultVal) === 'string') {
    //     this.rangeDefaultVal = this.rangeDefaultVal.split(' - ')
    //     this.currentValue[this.rangeN].configData.configData = this.rangeDefaultVal
    //   }
    // }, 1000)
  },
  methods: {
    changeRange (val) {
      console.log(val)
      this.options.disabledDate = (date) => {
        if (!val) {
          return false
        }
        return !moment(moment(date.valueOf()).format('YYYY-MM-DD')).isBetween(moment(val[0]).subtract(1, 'days'), moment(val[1]).add(1, 'd'))
      }
      this.currentValue[this.rangeN].configData.configData = val.join(' - ')
    },
    changeDefault (val) {
      console.log(val)
      this.currentValue[this.valueN].configData.configData = val
    },
    formatSelectChange (val) {
      if (val !== 'other') {
        this.configs[this.formatN].configData.configData = val
      }
    },
    otherDataChange () {
      if (this.formatSelectVal === 'other') {
        this.configs[this.formatN].configData.configData = this.otherData
      }
    },
    otherDataFocus () {
      this.formatSelectVal = 'other'
    }
  }
}
</script>

